
.container-panel-true{
 display: block;
 position: absolute;
 bottom: 0;
 width: 80%;
 height: 30%;
 transition: height 0.25s;
 margin:1px;
}
.container-panel-false{
 display: block;
 position: absolute;
 bottom: 0;
 width: 80%;
 height: 8%;
 transition: height 0.25s;
 margin:1px;
}

.main-panel{
 display:inline;
 position: absolute;
 bottom: 0;
 width: 100%;
 height: 60%;
}

.top-panel-true{
 display:inline;
 position: absolute;
 height: 40%;
 width: 80%;
 top: 0;
 left: 50%;
 transform: translate(-50%);
}

.top-panel-false{
 display:none;
}

.panel {
 border:none;
 border-top-left-radius: 2rem;
 border-top-right-radius: 2rem;
 width: 100%;
 height: 100%;
 background-color: #aaa;
 opacity:0.4;
}

#canvasP5 {
 position: absolute;
 top: 50%;
 left: 50%;
 transform: translate(-50%,-50%);
 animation-name: fadeIn;
 animation-duration: 2s;
 overflow-y: scroll;
}

#canvasP5home {
  margin:0;
  padding: 0;
  top:0px;
  position: absolute;
  width: 100%;
  z-index:-1000;
  display: block;
}

.panel-text {
 display: inline;
 position: absolute;
 background-color: #000;
 opacity:0.7;
 color: rgb(0,255,0);
 width: 20%;
 top: 25%;
 left: 50%;
 transform: translate(-50%);
 border-radius: 1rem;
 font-size: 0.8rem;
 padding: 0.5rem;
}
.panel-text-pos {
 text-align: center;
 transform: translate(100%);
}
.panel-text-date-true {
 text-align: center;
 transform: translate(75%);
}
.panel-text-date-false {
 display:none;
}


.command-textarea-true{
 border-radius: 1rem;
 background-color: #bbb;
 opacity:0.7;
 transition: opacity 0.25s;
}
.command-textarea-false{
 opacity:0.0;
 transition: opacity 0.25s;
}

.p-instruct-true{
 position: absolute;
 display: inline;
 background-color: #444;
 opacity:0.7;
 color:#0f0;
 width: 20%;
 border-radius: 1rem;
 font-size: 1.2rem;
 height: 40%;
 padding: 1rem;
 right: 4%;
 top:4%;
 z-index: 200;
}

.panel-text-place-label{
  display: inline;
  position: absolute;
  color:#0f0;
  width: 20%;
  top: 0%;
  left: 50%;
  transform: translate(-45%);
  font-size: 0.8rem;
}

.rightPanel {
  transform: translate(110%);
}

.avatar-home-true {
  display: inline;
  border-radius: 50%;
  position: absolute;
  top: 15%;
  transform: translate(-140%);
  width: 75px;
  margin: 0.1;
}

.avatar-home-false {
  display: none;
  margin: 0.1;
}
